/* Basics styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  /* overflow: hidden; */
  background-color: #ff002f;
  height: 100%;
  margin: 20px;
  padding: 0 20px;
}

button {
  outline: none;
  background: none;
  border: none;
  cursor: pointer;
  text-transform: capitalize;
  display: flex;
  align-items: center;
  justify-content: center;
}

button:disabled {
  cursor: no-drop;
  background-color: -internal-light-dark(
    rgba(239, 239, 239, 0.3),
    rgba(19, 1, 1, 0.3)
  );
  color: -internal-light-dark(rgba(16, 16, 16, 0.3), rgba(255, 255, 255, 0.3));
  border-color: -internal-light-dark(
    rgba(118, 118, 118, 0.3),
    rgba(195, 195, 195, 0.3)
  );
}

input,
textarea {
  outline: none;
  background: none;
  border: none;
}

textarea {
  width: 100%;
  padding: 10px;
}

a {
  text-decoration: auto;
  color: #000;
}

#root {
  width: 100%;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

/*start scrollbar */
::-webkit-scrollbar {
  width: 10px;
  background-color: transparent;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 4px #ccc;
  border-radius: 5px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #cdcdcd; /* #09c6f9 */
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #adadad;
}

/* end scrollbar */

.block input[type="number"] {
  width: 75px;
}

.block input,
.notes textarea {
  background-color: #9f9f9f33;
  margin-right: 10px;
  border-radius: 8px;
  font-size: 16px;
}
/* end basics */

@import url("./utils/style.css");
